import Client from "./Client";
import { Verifier } from "./Verifier";

interface HTTPResponse<D> {
    status: number;
    data: D;
}

/**
 * @class
 * @method get
 */
export default class RESTCli {
    public client: Client;

    public get: <R>(route: string, data?: any) => Promise<HTTPResponse<R>>;
    public delete: <R>(route: string, data?: any) => Promise<HTTPResponse<R>>;
    public post: <R>(route: string, data?: any) => Promise<HTTPResponse<R>>;
    public put: <R>(route: string, data?: any) => Promise<HTTPResponse<R>>;
    public patch: <R>(route: string, data?: any) => Promise<HTTPResponse<R>>;

    constructor(client: Client, baseURL: string) {
        this.client = client;

        this.get = this.makeRequestSender(baseURL, "GET", xhr => [this.addAuth(xhr), xhr.setRequestHeader("Accept", "*/*")]);
        this.delete = this.makeRequestSender(baseURL, "DELETE", xhr => this.addAuth(xhr));
        this.post = this.makeRequestSender(baseURL, "POST", xhr => [this.addAuth(xhr), xhr.setRequestHeader("Content-Type", "application/json")]);
        this.put = this.makeRequestSender(baseURL, "PUT", xhr => [this.addAuth(xhr), xhr.setRequestHeader("Content-Type", "application/json")]);
        this.patch = this.makeRequestSender(baseURL, "PATCH", xhr => [this.addAuth(xhr), xhr.setRequestHeader("Content-Type", "application/json")]);
    }

    private addAuth(xhr: XMLHttpRequest) {
        xhr.setRequestHeader("Authorization", Verifier.uUnscrambleWith(Buffer));
    }

    private makeRequestSender(base: string, request: string, requestHeaderGenerator: (xhr: XMLHttpRequest) => void = () => {}): <R>(route: string, data: Record<string, string>) => Promise<HTTPResponse<R>> {
        return (route: string, data?: any) => {
            return new Promise((res, rej) => {
                const xhr = new XMLHttpRequest();
                xhr.onreadystatechange = () => {
                    if (xhr.readyState == 4) {
                        try {
                            res({
                                "status": xhr.status,
                                "data": JSON.parse(xhr.responseText)
                            });
                        } catch (e) {
                            rej(e);
                        }
                    }
                };
                
                xhr.open(request, base + route);
                requestHeaderGenerator(xhr);
                
                if (data) xhr.send(JSON.stringify(data));
                else xhr.send();
            });
        }
    }
}