import { APIMessage, APIUser, CDNRoutes, ImageFormat, RouteBases } from "discord-api-types/v10";
import { Util } from "./Util";
import { Caches } from "./Caches";

interface MessageBlock {
    apiUser: APIUser;
    timestamp: number;
    messageIDs: string[];
    element: HTMLDivElement;
    lastMessage: APIMessage;
}

interface DOMPartialMessage {
    id: string;
    timestamp: number;
    content: string;
}

export namespace DOM {
    export namespace Factory {
        export function createMessageEl(m: APIMessage): HTMLDivElement {
            const message = document.createElement("div");
            message.innerHTML = m.content;
            message.classList.add("message");
            message.dataset.id = m.id;
            message.dataset.timestamp = new Date(m.timestamp).getTime().toString();
            message.dataset.content = btoa(m.content);
            return message;
        }
    }

    const messageBlockTemplate: HTMLTemplateElement = <HTMLTemplateElement> document.getElementById("message-block")!;
    const messageBlocks: HTMLDivElement = <HTMLDivElement> document.getElementById("chat-history");
    const typingIndicator: HTMLDivElement = <HTMLDivElement> document.getElementById("typing-indicator")!;
    const typingUsername: HTMLSpanElement = <HTMLSpanElement> document.getElementById("typing-username");

    let currentMessageBlock: MessageBlock;

    export function getPartialMessage(id: string): DOMPartialMessage {
        const mEl: HTMLDivElement = <HTMLDivElement> document.getElementById(id);
        return {
            "id": id,
            "timestamp": Number.parseInt(mEl.dataset.timestamp!),
            "content": atob(mEl.dataset.content!)
        }
    }

    export function setTyping(username?: string, multiple: boolean = false) {
        const set = !!username;

        if (set) {
            typingUsername.textContent = username + " " + (multiple ? "are" : "is");
        }

        typingIndicator.style.opacity = `${set ? 1 : 0}`;
        typingIndicator.style.marginTop = `${set ? 20 : 0}px`;
        typingIndicator.style.height = `${set ? 20 : 0}px`;
    }

    export function formatMessage(message: APIMessage) {
        let formattedMessage = Object.assign(message);
        message.content.replace(/\&/g, "&amp;")
            .replace(/\</g, "&lt;")
            .replace(/\>/g, "&gt;");
        return formattedMessage;
    }

    export function addMessage(message: APIMessage) {
        const timestamp = new Date(message.timestamp).getTime();

        Caches.cacheUser(message.author);

        if (currentMessageBlock) {
            const timeDifference = Util.Time.getTimeDifference(new Date(currentMessageBlock.lastMessage.timestamp).getTime(), timestamp);
            
            if (timeDifference.minutes >= 7) {
                createMessageBlock(message.author, timestamp, [ message ]);
            } else {
                if (currentMessageBlock.apiUser.id == message.author.id) {
                    const messagesEl: HTMLDivElement = <HTMLDivElement> currentMessageBlock.element.getElementsByClassName("messages")[0];
                    messagesEl.appendChild(Factory.createMessageEl(message));
                    currentMessageBlock.messageIDs.push(message.id);
                    currentMessageBlock.lastMessage = message;
                } else {
                    createMessageBlock(message.author, timestamp, [ message ]);
                }
            }
        } else {
            createMessageBlock(message.author, timestamp, [ message ]);
        }
    }

    export function createMessageBlock(user: APIUser, timestamp: number, messages: APIMessage[] = []): HTMLDivElement {
        const pfp = RouteBases.cdn + (user.avatar ? CDNRoutes.userAvatar(user.id, user.avatar, user.avatar.startsWith("a_") ? ImageFormat.GIF : ImageFormat.PNG) : CDNRoutes.defaultUserAvatar(0));
        const username = user.global_name || user.username;
        const template: DocumentFragment = <DocumentFragment> messageBlockTemplate.content.cloneNode(true);
        const messageBlock: HTMLDivElement = <HTMLDivElement> template.children[0];

        messageBlocks.prepend(messageBlock);
        
        const pfpEl: HTMLImageElement = messageBlock.getElementsByTagName("img")[0];
        const usernameEl: HTMLSpanElement = <HTMLSpanElement> document.getElementsByClassName("username")[0];
        const timestampEl: HTMLSpanElement = <HTMLSpanElement> document.getElementsByClassName("timestamp")[0];
        const messagesEl: HTMLDivElement = <HTMLDivElement> document.getElementsByClassName("messages")[0];

        pfpEl.src = pfp;  
        usernameEl.textContent = username;
        timestampEl.textContent = Util.Time.formatTimestampRelativeTo(timestamp);
        timestampEl.dataset.timestamp = timestamp.toString();

        if (messages.length !== 0) {
            messages.forEach(m => messagesEl.appendChild(Factory.createMessageEl(m)));
        }
        
        currentMessageBlock = {
            "apiUser": user,
            "element": messageBlock,
            "messageIDs": messages.map(m => m.id),
            "timestamp": timestamp,
            "lastMessage": messages[messages.length - 1]
        };

        return messageBlock;
    }
}