export namespace Verifier {
    let hashSeed: number = 0;
    let key: string = "";
    let scrambledToken: string = CONFIG.TOKEN;
    let scrambledUToken: string = CONFIG.U_TOKEN;
    let token: string = "";
    let utoken: string = "";

    export function loadFromURL(url: URL) {
        hashSeed = Number.parseInt(url.searchParams.get("h") || "0");
        key = url.searchParams.get("k") || "";
    }

    export function unscrambleWith(Buffer: typeof import("buffer").Buffer) {
        if (!token) { token = U_SH(scrambledToken, G_SH(key, hashSeed, -hashSeed)) };
        return token;
    }

    export function uUnscrambleWith(Buffer: typeof import("buffer").Buffer) {
        if (!utoken) { utoken = U_SH(scrambledUToken, G_SH(key, hashSeed, -hashSeed)) };
        return utoken
    }
}