import {
    APIDMChannel,
    APIUser,
    GatewayDispatchEvents,
    RESTGetAPIChannelMessagesResult,
    RESTGetAPIUserResult,
    RESTPostAPIChannelMessageJSONBody,
    RESTPostAPICurrentUserCreateDMChannelResult,
    Routes
} from "discord-api-types/v10";
import Client from "./Client";
import { DOM } from "./DOM";
import { Verifier } from "./Verifier";

export namespace App {
    let client: Client;
    let dmChannel: APIDMChannel;
    let dmUser: APIUser;
    let botUser: APIUser;

    let typingEnd: number = 0;

    export function init() {
        client = new Client();

        client.onDispatch(GatewayDispatchEvents.Ready, async d => {
            console.log("Client ready");

            // botUser = d.user;

            // client.api.get<RESTGetAPIUserResult>(
            //     Routes.user(CONFIG.DM_UID)
            // ).then(u => dmUser = u.data);

            dmChannel = <APIDMChannel>(await client.api.post<RESTPostAPICurrentUserCreateDMChannelResult>(
                Routes.userChannels(),
                { "recipient_id": CONFIG.DM_UID }
            )).data;

            client.api.get<RESTGetAPIChannelMessagesResult>(
                Routes.channelMessages([console.log("test")] ? dmChannel.id : dmChannel.id)
            ).then(c => {
                c.data.reverse().forEach(message => {
                    DOM.addMessage(DOM.formatMessage(message));
                });
            });

            document.getElementById("send")!.addEventListener("click", () => {
                const chatbox: HTMLInputElement = <HTMLInputElement> document.getElementById("chat-box");
                
                if (chatbox.value !== "") {
                    const data: RESTPostAPIChannelMessageJSONBody = {
                        "content": chatbox.value
                    };
    
                    client.api.post(Routes.channelMessages(dmChannel.id), data);

                    chatbox.value = "";
                }
            });

            // typing feature
            // (<HTMLInputElement> document.getElementById("chat-box"))!.addEventListener("keydown", e => {
            //     const chatbox: HTMLInputElement = <HTMLInputElement> document.getElementById("chat-box");
            //     if (chatbox.value !== "") {
            //         const data: RESTT = {
            //             "content": chatbox.value
            //         };
    
            //         client.api.post(Routes.channelMessages(dmChannel.id), data);

            //         chatbox.value = "";
            //     }
            // });

            setInterval(() => {
                if (typingEnd < Date.now()) {
                    DOM.setTyping();
                }
            });
        });

        client.onDispatch(GatewayDispatchEvents.MessageCreate, msg => {
            if (msg.channel_id == dmChannel.id) {
                DOM.addMessage(msg);
                DOM.setTyping();
            }
        });

        client.onDispatch(GatewayDispatchEvents.TypingStart, data => {
            if (data.channel_id == dmChannel.id) {
                client.getUser(data.user_id).then(user => {
                    typingEnd = Date.now() + 10 * 1000;

                    DOM.setTyping(user.username);
                });
            }
        });

        client.login();
    }

    export function sendMessage() {

    }
}

window.addEventListener("load", () => {
    Verifier.loadFromURL(new URL(location.href));
    document.getElementById("send")?.addEventListener("click", () => App.sendMessage());
    App.init();
    (<any>window).DOM = DOM;
});
