export namespace Util {
    export interface DynamicDivisionResult {
        count: number;
        remainder: number;
    }

    export function dynamicDivision(
        n: number,
        divisors: number[]
    ): DynamicDivisionResult {
        let d = Number(n);
        for (let i = 0; i < divisors.length; i++) {
            const divisor = divisors[i];
            if (d < divisor) return { count: i, remainder: d };
            d -= divisor;
        }
        return {
            count: divisors.length,
            remainder: d,
        };
    }

    export namespace Time {
        export const SECOND_MS = 1000;
        export const MINUTE_MS = SECOND_MS * 60;
        export const HOUR_MS = MINUTE_MS * 60;
        export const DAY_MS = 24 * HOUR_MS;
        
        export interface TimeDifference {
            days: number;
            hours: number;
            minutes: number;
            seconds: number;
        }

        export function getTimeDifference(
            a: number,
            b: number
        ) {
            let difference = Math.abs(a - b);
            const r = [DAY_MS, HOUR_MS, MINUTE_MS, SECOND_MS].map(ms => {
                const v = Math.floor(difference / ms);
                difference %= ms;
                return v;
            });
            
            return {
                days: r[0],
                hours: r[1],
                minutes: r[2],
                seconds: r[3]
            };
        }

        export function formatTimestampRelativeTo(
            timestamp: number,
            relative: number = Date.now()
        ): string {
            const relatives = {
                TODAY: "Today",
                YESTERDAY: "Yesterday",
            };
            const timeDifference = getTimeDifference(timestamp, relative);
            
            let time = new Date(timestamp);
            let timeString = (time.getHours() > 12 ? (time.getHours() - 12) : time.getHours()) + ":" + (time.getMinutes() < 10 ? "0" : "") + time.getMinutes() + (time.getHours() > 12 ? " PM" : " AM");

            if (timeDifference.days === 0) {
                return relatives.TODAY + " at " + timeString;
            } else if (timeDifference.days == 1) {
                return relatives.YESTERDAY + " at " + timeString;
            } else {
                return `${time.getMonth() + 1}/${time.getDate()}/${time.getFullYear()} ${timeString}`;
            }
        }
    }
}
