import {
    APIUser,
    GatewayDispatchPayload,
    GatewayHeartbeat,
    GatewayIdentify,
    GatewayIntentBits,
    GatewayMessageCreateDispatchData,
    GatewayMessageDeleteBulkDispatchData,
    GatewayMessageDeleteDispatchData,
    GatewayMessageReactionAddDispatchData,
    GatewayMessageReactionRemoveAllDispatchData,
    GatewayMessageReactionRemoveDispatchData,
    GatewayMessageReactionRemoveEmojiDispatchData,
    GatewayMessageUpdateDispatchData,
    GatewayOpcodes,
    GatewayReadyDispatchData,
    GatewayReceivePayload,
    GatewaySendPayload,
    GatewayTypingStartDispatchData,
    RESTErrorData,
    RESTGetAPIUserResult,
    RouteBases,
    Routes,
} from "discord-api-types/v10";
import { Verifier } from "./Verifier";
import RESTCli from "./RESTCli";
// import type { GatewayHeartbeat, GatewayIdentify, GatewayMessageCreateDispatch, GatewayMessageDeleteBulkDispatch, GatewayMessageDeleteDispatch, GatewayMessageReactionAddDispatch, GatewayMessageReactionRemoveAllDispatch, GatewayMessageReactionRemoveDispatch, GatewayMessageReactionRemoveEmojiDispatch, GatewayMessageUpdateDispatch, GatewayReadyDispatch, GatewayReceivePayload, GatewaySendPayload, GatewayTypingStartDispatch } from "discord.js";

interface PayloadMap {
    MESSAGE_CREATE: GatewayMessageCreateDispatchData;
    MESSAGE_DELETE: GatewayMessageDeleteDispatchData;
    MESSAGE_DELETE_BULK: GatewayMessageDeleteBulkDispatchData;
    MESSAGE_REACTION_ADD: GatewayMessageReactionAddDispatchData;
    MESSAGE_REACTION_REMOVE: GatewayMessageReactionRemoveDispatchData;
    MESSAGE_REACTION_REMOVE_ALL: GatewayMessageReactionRemoveAllDispatchData;
    MESSAGE_REACTION_REMOVE_EMOJI: GatewayMessageReactionRemoveEmojiDispatchData;
    MESSAGE_UPDATE: GatewayMessageUpdateDispatchData;
    READY: GatewayReadyDispatchData;
    TYPING_START: GatewayTypingStartDispatchData;
}

type DispatchHandler = (payload: GatewayDispatchPayload) => void;

export default class Client {
    public static readonly GATEWAY_URL = "wss://gateway.discord.gg";

    public _canIdentify: boolean = false;
    public _dead: boolean = false;

    public readonly api: RESTCli;
    public readonly ws: WebSocket;
    public dispatchHandlers: DispatchHandler[] = [];
    public heartbeatInterval: number = 0;
    public sequence: number | null = null;

    constructor() {
        this.api = new RESTCli(this, RouteBases.api);
        this.ws = new WebSocket(Client.GATEWAY_URL);

        this.ws.onopen = () => {
            this.ws.onmessage = e => {
                try {
                    const j: GatewayReceivePayload = JSON.parse(e.data);

                    if (j.s) this.sequence = j.s;

                    switch (j.op) {
                        case GatewayOpcodes.Dispatch: {
                            this.dispatchHandlers.forEach(h =>
                                h.apply(this, [j])
                            );
                            break;
                        }
                        case GatewayOpcodes.Heartbeat: {
                            this._sendHeartbeat();
                            break;
                        }
                        case GatewayOpcodes.Hello: {
                            this.heartbeatInterval = j.d.heartbeat_interval;
                            this._beginHeartbeat();
                            this.login();
                            break;
                        }
                    }
                } catch {}
            };
        };
        
        this.ws.onclose = e => {
            this._dead = true;
            console.error(`Websocket client closed: ${e.code}: ${e.reason}`);
        };

        this.ws.onerror = e => {
            console.error(`Websocket client errored: ${e}`);
        };
    }

    public login() {
        this._canIdentify ? this._identify() : (this._canIdentify = true);
    }

    public onDispatch<K extends keyof PayloadMap, E extends PayloadMap[K]>(
        event: K,
        callback: (payload: E) => void
    ) {
        this.dispatchHandlers.push(payload => {
            if (payload.t == event) {
                callback.apply(null, [<E>payload.d]);
            }
        });
    }

    public getUser(id: string, fromCache?: Map<string, APIUser>): Promise<APIUser> {
        return new Promise(res => {
            if (fromCache && fromCache.has(id)) {
                return res(fromCache.get(id)!);
            } else {
                this.api.get<RESTGetAPIUserResult>(Routes.user(id)).then(response => {
                    const user = response.data;
                    if (fromCache) {
                        fromCache.set(user.id, user);
                    }
                    res(user);
                });
            }
        });
    }

    public _beginHeartbeat() {
        if (this.heartbeatInterval) {
            this._sendHeartbeat();
            setTimeout(this._beginHeartbeat.bind(this), this.heartbeatInterval);
        }
    }

    public _identify() {
        const p: GatewayIdentify = {
            op: GatewayOpcodes.Identify,
            d: {
                intents:
                    GatewayIntentBits.DirectMessageReactions |
                    GatewayIntentBits.DirectMessageTyping |
                    GatewayIntentBits.DirectMessages |
                    GatewayIntentBits.GuildMembers |
                    GatewayIntentBits.Guilds |
                    GatewayIntentBits.MessageContent,
                properties: {
                    browser: "nswitchbot",
                    device: "nswitchbot",
                    os: "nswitchbot",
                },
                token: Verifier.uUnscrambleWith(Buffer),
            },
        };
        console.log("Identifying client...");
        this.sendPacket(p);
    }

    public _sendHeartbeat() {
        const p: GatewayHeartbeat = {
            op: GatewayOpcodes.Heartbeat,
            d: this.sequence,
        };
        this.sendPacket(p);
    }

    public sendPacket(packet: GatewaySendPayload) {
        if (!this._dead) {
            this.ws.send(JSON.stringify(packet));
        } else {
            console.log("Cannot send packet while connection is dead", packet);
        }
    }
}
